.create-container {
    position: fixed;
    top: 0;
    right: 0;
    left: 0;
    bottom: 0;
    z-index: 1;
    backdrop-filter: blur(10px);
    width: 100%;
    display: flex;
    justify-content: center;
    align-items: center;
}

.create-box {
    width: 8cm;
    border: 1px solid black;
    background-color: rgb(214, 244, 255);
    padding: .5cm .5cm;
}

.create-button{
    padding: .2cm;
    background-color: royalblue;
    color: white;
}

.input-room-name {
    padding: .2cm;
}

.create-form {
    display: flex;
    flex-direction: column;
    row-gap: .2cm;

}